.App {
  margin: 0;
}
.main-window {
  width: 200px;
  padding: 0 15px 10px 15px;
  margin-top: 0;
}
.row {
  display: block;
  width: 200px;
  margin-bottom: 5px;
  padding: 0 10px;
}
.title {
  float: left;
}
.table {
  width: 100px;
}
.result {
  float: right;
  text-align: right;
}
.name {
  font-weight: 245;
  font-size: 24px;
  width: 100%;
  margin-bottom: 10px;
  vertical-align: bottom;
  text-align: left;
  padding-left: 5px;
  color: rgb(8, 8, 8);
}

.image {
  text-align: right;
  float: right;
}
.option-collection {
  padding: 0;
}
option {
  padding: 5px 10px;
}

option:checked {
  background: linear-gradient(#5bc0de, #5bc0de);
}

.btn {
  width: 180px;
  margin-top: 10px;
  border: none;
}
.btn:focus {
  outline: none;
}
.spinner {
  padding: 108px 0px;
  width: 50px;
  margin: 0 60px 0 56px;
  text-align: center;
}
.message {
  height: 238px;
  padding: 145px 0;
  color: red;
  font-style: bold;
}

#option-box {
  border-top: lightgrey solid 0.1px;
  padding-top: 10px;
  width: 180px;
  margin-left: -5px;
}
.clock {
  text-align: right;
  padding: 5px 3px 10px 3px;
  border-bottom: 0.1px solid lightgrey;
  margin-bottom: 15px;
  font-size: 13px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  background: linear-gradient(#ebf5f8, #42a3c0);
  color: white;
}
.option-select {
  padding: 0;
}
